import React,{useEffect,useState}  from 'react' ;
import Card from 'react-bootstrap/Card'

import ItemMap from'./map' 
const Items=(props)=>
{
  
     const [photos,setPhotos]=useState([]) 
    const [bbox,setBbox]=useState(props.LongMin + ','+ props.LatMin +','+ props.LongMax + ','+ props.LatMax)
 
  

    useEffect(()=>
    {
        setBbox(props.LongMin + ','+ props.LatMin +','+ props.LongMax + ','+ props.LatMax)
        console.log("items" , props.LongMin ,props.LatMin ,props.LongMax , props.LatMax)
       let url = 'https://api.flickr.com/services/rest/?method=flickr.photos.search&api_key=f7b4a1de41e2682f312f3011e302bd5c&format=json&nojsoncallback=1&bbox='+bbox+'&extras=url_o,geo&accuracy=5' ;
        try{
            fetch(url)
        .then(response => response.json())
        .then(data => { if(data.photos){setPhotos(data.photos.photo)} else {
            setPhotos([])  
        } } )}
        catch(err){ console.log(err)} 
       
    },[ props.LongMin ,props.LatMin ,props.LongMax , props.LatMax , bbox ])  ;
    //&bbox=31,7,32,8
    // 20.7076,55.2352,21.9485,56.102
    // casa - rabat : &bbox=-7.589843, 33.573110,-6.849813, 33.971590&
   
        if(photos.length!==0)
        return( 
            
            <div className="items" >
                   { photos.filter(photo => photo.url_o).map(photo => <div className="item" key={photo.id} >
                     
                  
                   <Card >
                         <Card.Img variant="top" src={photo.url_o} />

 
  <Card.Body>
    <Card.Title> {photo.title}</Card.Title>
    {console.log("yooooo ")}
    <Card.Text>
        <p> latitude  : {photo.latitude} </p>
        <p> longitude : {photo.longitude} </p>
         </Card.Text>
    <ItemMap  classname="item-btn" text="show user location" longitude={photo.longitude} latitude={photo.latitude} setStates={props.setStates} ></ItemMap>
  </Card.Body>
</Card> 
                        </div> )}

            </div>

        );
        else return(<h1 className="not-found"> Photos Not Found </h1>)
    
}

export default Items ;
