import React from 'react' ; 
import Form from 'react-bootstrap/Form';

const changeHandler=(e,setStates)=>
   {
     if(e.target.name==="longMin")
     {
          setStates.setLongMin(e.target.value)
     }
  
     if(e.target.name==="longMax")
     {
      setStates.setLongMax(e.target.value)
     }
     if(e.target.name==="latMin")
     {
       console.log(e.target.value)
      setStates.setLatMin(e.target.value)
     }
     if(e.target.name==="latMax")
     {
      setStates.setLatMax(e.target.value) 
     }
   
     
       
       
  }

 const FormComponent=(props)=>{
 return(
  <div className="form">

<Form >
 
  <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
    <Form.Label>Longitude max </Form.Label>
    <Form.Control type="input" placeholder={props.LongMax} name="longMax" required onChange={e=>changeHandler( e,props.setStates)}   />
    <Form.Label>Longitude min </Form.Label>
    <Form.Control type="input" placeholder={props.LongMin} name="longMin" onChange={e=>changeHandler( e,props.setStates)} required />
    <Form.Label>Latitude max </Form.Label>
    <Form.Control type="input" placeholder={props.LatMax} name="latMax"  required onChange={e=>changeHandler(e, props.setStates)} />
    <Form.Label>Latitude min </Form.Label>
    <Form.Control type="input" placeholder={props.LatMin}  name="latMin" required  onChange={e=>changeHandler(e, props.setStates)} />
  </Form.Group>
 
</Form>
 
</div>) ;
}
    
 // i don't know if this is good practice
 


//Use uncontrolled components
//Dont forget to assign the references with React.createRef
//static getDerivedStateFromProps



export default FormComponent ; 
