import React ,{useState,useEffect} from'react' 
import 'bootstrap/dist/css/bootstrap.min.css';
import Items from './items'
import FormModal  from './modal';
import ItemMap from './map'
import './index.css'
 const App=()=>
{
    const [longMax,setLongMax] = useState('42') 
    const [longMin,setLongMin] = useState('41') 
    const [latMax,setLatMax] = useState('3') 
    const [latMin,setLatMin] = useState('2') 
  

   
   return(
  
    <div className="main">
  
  { console.log("app:" , longMax , longMin ,latMax , latMin )}
  <header>
      <div className="logo">
      <a href="/" style={{ "width":"auto"}}>
         <img  src={window.location.origin +"/images/logo.png"} alt=""   width="30%" />  </a>   </div>  
    <ItemMap  classname="search-map-btn" longitude={longMax} latitude={latMax} setStates={{setLatMax,setLatMin,setLongMax , setLongMin}} ></ItemMap>
    <FormModal  LongMin={longMin}  LatMin={latMin} LongMax={longMax} LatMax={latMax}  setStates={{setLatMax,setLatMin,setLongMax , setLongMin}} /> 
      
      
      </header>
     <Items  LongMin={longMin}  LatMin={latMin} LongMax={longMax} LatMax={latMax} setStates={{setLatMax,setLatMin,setLongMax , setLongMin}} /> 
    </div>  
       )
}

export default App 