import React ,{useState,useEffect} from "react";
import { MapContainer, TileLayer, Marker ,Popup ,useMapEvents } from 'react-leaflet';

import Modal from 'react-bootstrap/Modal'
import { Button } from 'react-bootstrap'
function LocationMarker(props) {
  const [change,setChange]=useState(false)
    const [position, setPosition] = useState(null)
    const map = useMapEvents({
      click() {
        map.locate()
      },
      locationfound(e) {
        setPosition(e.latlng)
        map.flyTo(e.latlng, map.getZoom())
      },
    })
   
    map.on('click', async function(e){
        var coord = e.latlng;
        await console.log("You clicked the map at latitude: " + coord.lat + " and longitude: " + coord.lng)
        props.handleClose()
       props.setStates.setLongMax(coord.lng+0.1);
      
        props.setStates.setLatMin(coord.lat-0.1) ;
      
      props.setStates.setLatMax(coord.lat+0.1) ;
        
   props.setStates.setLongMin(coord.lng-0.1) ;
       
        
       
        
        
        // console.log("and the max are : " , props.longitude , props.latitude )
        });
  
    return position === null ? null : (
      <Marker position={position}>
        <Popup>You are here</Popup>
      </Marker>
    )
  }
  
const ItemMap=(props)=>{
    const [show, setShow] = useState(false);
  
    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);
  
    return (
      <div>
        {/* {console.log(props.longitude , props.latitude , " map re-rendered")} */}
      { 
      
      props.classname=="item-btn" ? <Button className={props.classname}  variant="primary" onClick={handleShow}  >
      
         <i  style={{"color ":"white "}} class="fas fa-map-marker-alt"></i>
        </Button> :
        <Button className={props.classname}  variant="primary" onClick={handleShow}  >
          <i style={{"color ":"white "}} class="fas fa-map-marked-alt"></i>
        </Button>
      
      }  
  
        <Modal centered  fullscreen={true} classname="map-modal" show={show} onHide={handleClose}>
          <Modal.Header closeButton>
            
          </Modal.Header>
          <Modal.Body  >
             
          <div style={{ "width":"100%" }}>
          <MapContainer center={[ props.latitude , props.longitude]} zoom={5} scrollWheelZoom={true}>
  <TileLayer
    attribution='&copy; <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
    url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
  />
  <Marker position={[ props.latitude , props.longitude ]}>
  
  </Marker>
  <LocationMarker handleClose={handleClose}    setStates={props.setStates}/>
</MapContainer>
          </div>
   
        
    </Modal.Body>
          <Modal.Footer>
            <Button variant="secondary" onClick={handleClose}>
              Close 
            </Button>
            
          </Modal.Footer>
        </Modal>
      </div>
    );
  }
  

  export default ItemMap ; 