import React ,{useState} from 'react' 
import FormComponent from './Form'
import Modal from 'react-bootstrap/Modal'
import { Button } from 'react-bootstrap'
const FormModal=(props)=> {
    const [show, setShow] = useState(false);
  
    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);
  
    return (
      <>
        <Button className="search-btn" variant="primary" onClick={handleShow}  >
        <i  style={{"color ":"white "}} class="fas fa-search-location"></i>
        </Button>
  
        <Modal show={show} onHide={handleClose}>
          <Modal.Header closeButton>
            <Modal.Title> Enter coordinates  </Modal.Title>
          </Modal.Header>
          <Modal.Body>
          <FormComponent LatMin={props.LatMin} LatMax={props.LatMax} LongMin={props.LongMin} LongMax={props.LongMax } setStates={props.setStates} /> </Modal.Body>
          <Modal.Footer>
            <Button variant="secondary" onClick={handleClose}>
              Close 
            </Button>
            
          </Modal.Footer>
        </Modal>
      </>
    );
  }
  
 export default FormModal ;